import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
var n=new Date();
var y=n.getFullYear();
var m=n.getMonth()+1;
var d=n.getDate();
    
export default new Vuex.Store({
  state: {
    entrar:false,
    categoria:'',
    Carrito:0,
    Pertenece:false,
    Admin:{},
    estaCompra:[],
    total:0,
    orden:{
      orderID : '1254656',
      customerID :'514866',
      employeeID : '6',
      orderDate :y+'-'+m+'-'+d,
      requiredDate :y+'-'+m+'-'+(d+1),
      shippedDate : y+'-'+m+'-'+d,
      shipVia :0,
      freight :0,
      shipName :'',
      shipAddress : {
      street : '',
      city :'',
      region :'',
      postalCode :'',
      country : ''
      },
      details:[]
     }
  },
  mutations: {
    AgregarAlCarrito(state,Product){
      console.log(state.estaCompra)
      
      for(var item in state.estaCompra){
        console.log(state.estaCompra[item],68,Product)
        if(state.estaCompra[item]._id===Product._id){
          state.Pertenece=true;
        }
      } 
      if(!state.Pertenece){
        state.estaCompra.push(Product)
      }
      else{
        
      }

      state.total+=Product.unitPrice; 
      state.Carrito++;
      console.log(state.total);  
    },
    AgregarOrden(state,Producto){
        if(state.Pertenece){
          for(var item in state.orden.details){
              if(state.orden.details[item].productID===Producto.productID){
              state.orden.details[item].quantity++;
            }
        }
       }else{
         
        state.orden.details.push(
                {
                productID :Producto.productID,
                unitPrice : Producto.unitPrice,
                quantity : 1,
                discount : 0
               }
                  
       )
      }
       
    },
    Aumentar(state,precio){
      state.total+=precio;
      state.Carrito++;
    },
    Disminuir(state,precio){
      state.total-=precio;
      state.Carrito--;
    },
    Modificar(state,inform){
        
          state.orden.shipAddress.street=inform.street;
          state.orden.shipAddress.city=inform.city;
          state.orden.shipAddress.region=inform.Region;
          state.orden.shipAddress.postalCode=inform.PostalCode;
          state.orden.shipAddress.country=inform.Country;
        
    },
    Vaciar(state){
      state.estaCompra=[];
      state.orden=[];
      state.total=0;
    },
    AdminActual(state,item){
      state.Admin=item;
      state.entrar=true;
    },
    SelecCat(state,cat){
      state.categoria=cat
      console.log(state.categoria)
    }
    
  },
  actions: {
  },
  modules: {
  }
})
